export class AppData {
    constructor(events) {
        this.events = events;
        this.items = [];
        this.basket = {
            items: [],
            total: 0,
        };
        this.preview = null;
        this.order = {
            email: '',
            phone: '',
            address: '',
            payment: 'card',
            total: 0,
            items: [],
        };
        this.formErrorsOrder = {};
        this.formErrorsContacts = {};
    }
    setItems(items) {
        this.items = items;
        this.events.emit('items:change', this.items);
    }
    setPreview(item) {
        this.preview = item;
        this.events.emit('preview:change', item);
    }
    inBasket(item) {
        return this.basket.items.includes(item.id);
    }
    addToBasket(item) {
        this.basket.items.push(item.id);
        this.basket.total += item.price;
        this.events.emit('basket:change', this.basket);
    }
    removeFromBasket(item) {
        this.basket.items = this.basket.items.filter((id) => id != item.id);
        this.basket.total -= item.price;
        this.events.emit('basket:change', this.basket);
    }
    clearBasket() {
        this.basket.items = [];
        this.basket.total = 0;
        this.events.emit('basket:change');
    }
    setPaymentMethod(method) {
        this.order.payment = method;
    }
    setOrderField(field, value) {
        if (field === 'payment') {
            this.setPaymentMethod(value);
        }
        else {
            this.order[field] = value;
        }
        if (this.order.payment && this.validateOrder()) {
            this.order.total = this.basket.total;
            this.order.items = this.basket.items;
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.address) {
            errors.address = 'Укажите адрес';
        }
        this.formErrorsOrder = errors;
        this.events.emit('formErrorsOrder:change', this.formErrorsOrder);
        return Object.keys(errors).length === 0;
    }
    setContactsField(field, value) {
        this.order[field] = value;
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
    }
    validateContacts() {
        const errors = {};
        const email_regex = /([a-zA-Z0-9._-]+@[a-zA-Z0-9._-]+.[a-zA-Z0-9_-]+)/;
        if (!this.order.email) {
            errors.email = 'Укажите email';
        }
        if (!email_regex.test(this.order.email)) {
            errors.email = 'Формат email некорректный';
        }
        if (!this.order.phone) {
            errors.phone = 'Укажите телефон';
        }
        this.formErrorsContacts = errors;
        this.events.emit('formErrorsContacts:change', this.formErrorsContacts);
        return Object.keys(errors).length === 0;
    }
}
