import { Form } from './common/Form';
import { ensureElement } from '../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._paymentCard = ensureElement('.button_alt[name=card]', this.container);
        this._paymentCash = ensureElement('.button_alt[name=cash]', this.container);
        this._paymentCard.addEventListener('click', () => {
            this.payment = 'card';
            this.onInputChange('payment', 'card');
        });
        this._paymentCash.addEventListener('click', () => {
            this.payment = 'cash';
            this.onInputChange('payment', 'cash');
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    set payment(value) {
        this._paymentCard.classList.toggle('button_alt-active', value === 'card');
        this._paymentCash.classList.toggle('button_alt-active', value === 'cash');
    }
}
